import { useEffect, useRef, useState } from "react";
import * as d3 from "d3";
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import { Box } from "@mui/material";

const svgUrl = `${process.env.PUBLIC_URL}/images/Outlook.svg`;

const textMap = {
    "residual": {
        title: "Residualeinkommen",
        subtitle: "Wer mehr zahlen muss, hat weniger Spielraum",
        text: [
          "Das Haushaltseinkommen setzt sich aus verschiedenen Quellen zusammen, wie etwa Anstellungen, eigenstndigem Einkommen, Pensionen oder Sozialleistungen. Dabei ist der Lohn aus Erwerbsttigkeit zwar oft der grte Posten, doch lngst nicht der einzige. Besonders bei Haushalten mit Kindern, Pensionist:innen oder Menschen mit Untersttzungsbedarf spielen staatliche Leistungen und Transfers eine zentrale Rolle.",
          "Jedoch spielt nicht nur das Einkommen eine Rolle, viel mehr geht es um die Verfgbarkeit nach Abzug notwendiger Ausgaben. Dazu zhlen Wohnen, Heizen, Energie, Lebensmittel, Mobilitt sowie Kosten fr Bildung und Gesundheit. Diese notwendigen Ausgaben knnen jedoch stark variieren, etwa je nach Region, Wohnform oder dem Zugang zu ffentlicher Infrastruktur. Alleinerziehende, Pensionist:innen aber auch Mieter:innen stehen unter erhhtem finanziellem Druck. Sie haben oft weniger Gestaltungsspielraum als andere Personen mit vergleichbarerem nominellem Einkommen.",
          "Auch im regionalen Vergleich zeigen sich deutliche Unterschiede: Whrend Stdte wie Salzburg oder Innsbruck hhere durchschnittliche Ausgaben aufweisen, bleibt das sogenannte Residualeinkommen  also das Einkommen nach Abzug der notwendigen Ausgaben  dort teils hher als in Graz, Linz oder Wien. Das verdeutlicht, dass nicht allein die Hhe des Einkommens entscheidend ist, sondern vor allem, wie viel davon real verfgbar bleibt. Manahmen sollen also rumliche und strukturelle Unterschiede ernst nehmen und Spielrume schaffen, um auf lokale Gegebenheiten angemessen reagieren zu knnen."
        ],        
        color: "#f0ae9f"
    },
    "soz-infra": {
        title: "Soziale Infrastrukturen",
        subtitle: "Wenn soziale Infrastruktur zum Standortvorteil wird",
        text: [
          "Soziale Infrastrukturen sind genauso essenziell wie technische Netze wie Straen oder Telefonverbindungen. Zugang zu Kindergrten, Schulen, rzt:innen, Krankenhusern, Pflegeeinrichtungen und hnlichen sozialen Angeboten ist unverzichtbar fr das Funktionieren unserer Gesellschaft. Dennoch diese Einrichtungen nicht berall in gleicher Qualitt oder Dichte verfgbar, was regionale Unterschiede in der Lebensqualitt verstrken kann. Haushalte in gut ausgestatteten Gemeinden profitieren auf mehreren Ebenen: Sie sparen Wege, Zeit und Kosten, insbesondere fr Betreuung, Gesundheit oder Bildung. Das erhht nicht nur die reale Verfgbarkeit von Einkommen, sondern wirkt sich auch positiv auf Erwerbschancen, Familienorganisation und die Vereinbarkeit von Beruf und Alltag aus.",
          "Diese Unterschiede verdeutlichen, dass der Ausbau und Erhalt sozialer Infrastruktur ein zentraler Bestandteil jeder nachhaltigen Daseinsvorsorge ist und somit auch ein Hebel zur Reduktion sozialer und regionaler Ungleichheit sein kann."
        ],
        color: "#a5cdc8"
    },
    "zeit": {
        title: "Zeitverwendung",
        subtitle: "Armut ist oft auch Zeitmangel",
        text: [
          "Jeder Mensch verfgt ber dieselben 24 Stunden am Tag  doch wie diese Zeit genutzt wird, variiert stark. Die Verteilung von Zeit fr bezahlte Arbeit, unbezahlte Ttigkeiten wie Haushalt und Kinderbetreuung, Freizeit, Wegzeiten sowie Schlaf ist alles andere als gleichmig. Diese Differenzen haben tiefgreifende Auswirkungen auf die Lebensqualitt und beeinflussen mageblich die Mglichkeiten, Einkommen zu erzielen oder soziale Kontakte und Aktivitten wahrzunehmen.",
          "Frauen bernehmen deutlich mehr Care-Arbeit als Mnner, ein Anteil, der mit steigender Kinderzahl weiter zunimmt. Hinzu kommt, dass mangelnde Verfgbarkeit oder schlechte Erreichbarkeit sozialer Infrastruktur den Zeitaufwand fr unbezahlte Sorgearbeit erheblich erhht. Dadurch entstehen zustzliche Belastungen, die nicht nur die individuelle Lebensgestaltung einschrnken, sondern auch gesellschaftliche Ungleichheiten verstrken. Rahmenbedingungen sollten daher besser darauf ausgerichtet werden, Zeit als wertvolle Ressource anzuerkennen und beispielsweise durch den Ausbau sozialer Infrastruktur dazu beitragen, die ungleiche Verteilung von Arbeits- und Sorgezeiten zu verringern."
        ],
        color: "#fcd799"
    },
};

const OutlookChart = ( {altText} ) => {
  const svgRef = useRef(null);

  const initialId = Object.keys(textMap)[0]; // Get first key from textMap
  const [selectedInfo, setSelectedInfo] = useState(textMap[initialId]);

  const navigateText = (direction) => {
    const keys = Object.keys(textMap);
    const currentIndex = keys.indexOf(Object.keys(textMap).find(key => textMap[key] === selectedInfo));
    let newIndex = currentIndex + direction;
  
    if (newIndex < 0) newIndex = keys.length - 1; // Loop to last item
    if (newIndex >= keys.length) newIndex = 0; // Loop to first item
  
    setSelectedInfo(textMap[keys[newIndex]]);
  };

  useEffect(() => {
    d3.xml(svgUrl).then((data) => {
      if (svgRef.current) {
        svgRef.current.innerHTML = "";
        svgRef.current.appendChild(data.documentElement);
  
        const svg = d3.select(svgRef.current).select("svg");
  
        svg.attr("width", "100%").attr("height", "100%").attr("alt", altText);
        svg.style("display", "block").style("margin", "auto");
        
        // if (!isTouchDevice()) {
          svg.selectAll("g")
            .filter(function () {
              const id = d3.select(this).attr("id");
              return Object.keys(textMap).includes(id);
            })
            .style("cursor", "pointer")
            .on("mouseover", function () {
              d3.select(this).selectAll("*").each(function () {
                const currentColor = d3.select(this).style("fill");
                const darker = d3.color(currentColor)?.darker(0.25); // Darken
                d3.select(this).attr("data-original-fill", currentColor); // Save original
                d3.select(this).style("fill", darker);
              });
            })
            .on("mouseout", function () {
              d3.select(this).selectAll("*").each(function () {
                const original = d3.select(this).attr("data-original-fill");
                if (original) d3.select(this).style("fill", original);
              });
            })
            .on("click", function () {
              const id = d3.select(this).attr("id");
              const info = textMap[id] || { title: "Unbekannt", text: "Keine Beschreibung verfgbar.", color: "#ccc" };
            
              setSelectedInfo(info);
            });
        // }
      }
    });
  }, [altText]);

  return (
    <div className="outlook-chart-wrapper">
      <div ref={svgRef} style={{ width: "100%", maxWidth: "640px", overflow: "hidden", justifyContent: "center" }} />

      <Box 
        className="infotext-container"
        sx={{
          boxShadow: 2, // Use shadow from MUI's theme (0-24)
          padding: { xs: 1, sm: 2 },
          pt: { xs: 2, sm: 2 },
          borderRadius: 2,
          backgroundColor: selectedInfo.color
        }}
        style={{marginTop: "1rem"}}
      >
        <div className="info-navigation">
          <button onClick={() => navigateText(-1)} className="nav-button" aria-label="back">
            <ArrowBackIosNewIcon />
          </button>
          <h3 style={{ margin: "0", fontSize: "18px", fontWeight: "bold", paddingTop: "4px"}}>{selectedInfo.title}</h3>
          <button onClick={() => navigateText(1)} className="nav-button" aria-label="forward">
            <ArrowForwardIosIcon />
          </button>
        </div>
        <h4 style={{ margin: "0", fontSize: "18px", fontWeight: "bold", padding: "3px 0 3px"}}>{selectedInfo.subtitle}</h4>
        <div style={{ marginTop: "1em"}}>
          {selectedInfo.text.map((paragraph, index) => (
            <p key={index} style={{ margin: "0.6em"}}>
              {paragraph}
            </p>
          ))}
        </div>
      </Box>
    </div>
  );
};

export default OutlookChart;