import {Box} from '@mui/material';
import { useLanguage } from '../../context/LanguageContext';
import OutlookChart from '../OutlookChart';

const OutlookSection = () => {

    const { language } = useLanguage();

    const translations = {
        DE: {
            header: 'Fazit',
            content: [
                {
                    id: 1,
                    text: (
                    <>
                        Soziale Ungleichheit lsst sich nicht allein durch Einkommensunterschiede erklren. 
                        Zwar ist das Einkommen ein zentraler Faktor, der den finanziellen Handlungsspielraum von Haushalten bestimmt  doch ein vollstndigeres Bild entsteht erst, 
                        wenn man weitere Aspekte wie notwendige Ausgaben, den Zugang zu ffentlicher Infrastruktur und die zeitlichen Ressourcen von Menschen bercksichtigt.
                    </>
                    ),
                },
                {
                    id: 2,
                    text: (
                    <>
                        Diese Dimensionen wirken nicht isoliert, sondern sind eng miteinander verknpft: 
                        Wer wenig verdient, aber zustzlich hohe Fixkosten trgt oder keinen Zugang zu untersttzenden Angeboten hat, ist strker belastet  sowohl finanziell als auch organisatorisch. 
                        Gleichzeitig kann ein eingeschrnkter Zugang zu ffentlicher Infrastruktur die Notwendigkeit erhhen, Zeit und Geld in private Lsungen zu investieren. 
                        Auch die Verteilung der verfgbaren Zeit  etwa zwischen Erwerbsarbeit, unbezahlter Sorgearbeit und Erholung  ist ungleich verteilt und beeinflusst soziale Teilhabe.
                    </>
                    ),
                },
                {
                    id: 3,
                    text: (
                    <>
                       Das Zusammenspiel dieser Faktoren macht deutlich: Ungleichheit ist mehrdimensional. 
                       Ein vertieftes Verstndnis sozialer Ungleichheit erfordert daher einen ganzheitlichen Blick auf Lebensrealitten. 
                       Nur wenn man diese Zusammenhnge ernst nimmt, knnen gerechte und wirksame Antworten auf soziale Herausforderungen gefunden werden.
                    </>
                    ),
                },
                {
                    id: 4,
                    isChart: true,
                    alt: "Diagramm mit vier berlappenden Kreisen, welche die Themenbereiche dieser Webseite visualisieren"
                }
            ]
        },
        EN: {
            header: 'Outlook',
            content: [
            ]
        }
    };

    const section = translations[language] || translations.DE;

    return (
        <div>
            <h2 className="section-header">{section.header}</h2>
            {section.content.map((item) =>
                item.isChart ? (
                    <Box 
                        key={item.id}
                        sx={{
                            background: "#f4f4f4",
                            padding: { xs: 1, sm: 2 },
                            borderRadius: 1,
                            boxShadow: 1,
                            fontSize: '14px',
                            marginTop: 3,
                            display: 'flex', 
                            justifyContent: 'center', 
                            alignItems: 'center',
                            flexDirection: 'column'
                        }}
                    >
                        <div className='outlook-chart'>
                            <OutlookChart altText={item.alt} />
                        </div>
                    </Box>
                ) : (
                <p key={item.id} className={`paragraph ${item.className || ''}`}>
                    {item.text}
                </p>
                )
            )}
        </div>
    );
};

export default OutlookSection;