import { useLanguage } from '../../context/LanguageContext';
import GlossaryTerm from '../GlossaryTerm';
import { Accordion, AccordionSummary, AccordionDetails, Typography } from '@mui/material';
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";


const translations = {
        DE: {
            content: [
                {
                    id: 1,
                    text: <> Wie viel Einkommen haben die Haushalte? </>,
                    isContentHeader: true,
                },
                {
                    id: 2,
                    text: (
                    <>  
                        Das Haushaltseinkommen ist ein zentraler Indikator fr die Messung des Lebensstandards der Menschen in sterreich und dient oftmals als Basis der Berechnung Ungleichheit im Land. 
                        Um eine przisere Analyse der Einkommensverteilung zu ermglichen, wird das Haushaltseinkommen hufig in sogenannte <GlossaryTerm className="glossary-term quintil" sectionId="residualIncome">Quintile</GlossaryTerm> eingeteilt. 
                        Aber was bedeutet das genau, und warum ist diese Einteilung sinnvoll?
                    </>
                    ),
                },
                {
                    id: 3,
                    accordionHeader: "Was sind Quintile?",
                    accordionFirstText: ( 
                    <>  
                        Ein <GlossaryTerm className="glossary-term quintil" sectionId="residualIncome">Quintil</GlossaryTerm> beschreibt jeweils genau ein Fnftel der Haushalte, geordnet nach ihrem Einkommen. Das bedeutet:
                    </>
                    ),
                    accordionSubheader: (
                    <>
                        <span className="accordion-subheader" >Warum teilen wir das Haushaltseinkommen in Quintile?</span>
                    </>
                    ),
                    accordionSecondText: ( 
                    <>  
                        Die Einteilung in <GlossaryTerm className="glossary-term quintil" sectionId="residualIncome">Quintile</GlossaryTerm> hilft, die Einkommensverteilung und 
                        wirtschaftliche Ungleichheiten besser sichtbar zu machen. 
                        Whrend Durchschnittswerte oft verzerrt sein knnen (z. B. durch extreme Einkommen an der Spitze), 
                        zeigen Quintile deutlicher, wie Einkommen auf verschiedene gesellschaftliche Gruppen verteilt sind.<br/><br/>
                    </>
                    ),
                    list: [
                        { title: 'Erstes Quintil: ', text: 'Die 20 % der Haushalte mit dem niedrigsten Einkommen' },
                        { title: 'Zweites Quintil: ', text: 'Haushalte mit Einkommen zwischen den unteren 20 % und 40 % aller Haushalte' },
                        { title: 'Drittes Quintil: ', text: 'Die mittleren 20 % der Haushalte in der Einkommensverteilung' },
                        { title: 'Viertes Quintil: ', text: 'Haushalte mit Einkommen zwischen den oberen 60 % und 80 % aller Haushalte' },
                        { title: 'Fnftes Quintil: ', text: 'Die 20 % der Haushalte mit dem hchsten Einkommen' },
                    ],
                    isAccordion: true,
                    itemName: 'quintil-accordion'
                },
                {
                    id: 4,
                    isChart: true,
                    alt: "Diagramm mit 5 Balken, die das mittlere Haushaltseinkommen pro Einkommensquintil in sterreich darstellen.",
                    source: "Datenquelle: Konsumerhebung 2019/2020, Statistik Austria",
                },
                {
                    id: 5,
                    text: (
                    <>  
                        Das Einkommen bildet auch die zentrale Grundlage fr die Berechnung des <GlossaryTerm className="glossary-term residualIncome" sectionId="residualIncome">Residualeinkommens</GlossaryTerm>, 
                        jenes Teils des Einkommens, der nach Abzug der <GlossaryTerm className="glossary-term expenditure" sectionId="residualIncome">notwendigen Ausgaben</GlossaryTerm> verbleibt. 
                        Deshalb betrachten wir es im Folgenden noch einmal genauer.
                    </>
                    ),
                },
                {
                    id: 6,
                    text: (
                    <>  
                        Das Haushaltseinkommen in sterreich (nach Steuern) betrug 2019/2020 2.632 . Dabei zeigt sich eine deutliche <GlossaryTerm className="glossary-term incomeInequality" sectionId="residualIncome">Einkommensungleichheit</GlossaryTerm> zwischen den verschiedenen Bevlkerungsgruppen. 
                        So lag das mittlere Einkommen des ersten <GlossaryTerm className="glossary-term quintil" sectionId="residualIncome">Quintils</GlossaryTerm>, also jenes Fnftels der Haushalte mit den niedrigsten Einkommen,
                        bei lediglich 1.247  und damit deutlich unter dem sterreichweiten <GlossaryTerm className="glossary-term median" sectionId="residualIncome">Median</GlossaryTerm>. 
                        Im Gegensatz dazu verfgte das oberste Quintil, also die einkommensstrksten 20% der Haushalte, ber ein mittleres Einkommen von ber 4.500 . 
                        Das bedeutet, dass Haushalte im obersten Quintil im Schnitt mehr als 3.000  monatlich mehr zur Verfgung haben als jene im untersten Quintil.
                    </>
                    ),
                },
            ]
        },
        EN: {
            content: []
        }
    };

const IncomeSection = () => {

    const { language } = useLanguage();

    const section = translations[language] || translations.DE;

    return (

        <div>
            {/* <h2 className="section-header">{section.header}</h2> */}
            {section.content.map((item) =>
                
               item.isContentHeader ? (
                    <h3 key={item.id} className={`content-header ${item.className || ''}`}>{item.text}</h3>
                ) : item.isChart ? (
                    <div key={item.id} className="image-container median-hh-income-container">
                        <img src={`${process.env.PUBLIC_URL}/images/00_Median_HH_Income_Personen_DE.png`} alt={`${item.alt}`} />
                        <p className='source' style={{margin: "0 auto 12px"}}>{item.source}</p>
                    </div>
                ) : item.isAccordion ? (
                    <div key={item.id} style={{ margin: '24px 0' }}>
                        <Accordion sx={{ background: '#f4f4f4' }}>
                            <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="h4" sx={{ fontSize: '1.125rem' }}>
                                {item.accordionHeader}
                            </Typography>
                            </AccordionSummary>
                            <AccordionDetails>
                            <Typography>{item.accordionFirstText}</Typography>

                            {item.list && (
                                <ul className="custom-list income">
                                    {item.list.map((step, index) => (
                                        <li key={index}>
                                            <span className="boldText">{step.title}</span> {step.text}
                                        </li>
                                    ))}
                                </ul>
                            )}
                            <Typography style={{ marginBottom: '0.5rem' }}>{item.accordionSubheader}</Typography>
                            <Typography>{item.accordionSecondText}</Typography>
                            </AccordionDetails>
                        </Accordion>
                    </div>
                ) : (
                    <p key={item.id} className={`paragraph ${item.className || ''}`}>
                        {item.text}
                    </p>
                )
            )}
        </div>
    );
};

export default IncomeSection;